import {createSlice} from '@reduxjs/toolkit'

const snackBarSlice = createSlice({

    name: "snackBar",
    initialState: {
        shown: false,
        message: "",
        type: "info"
    },
    reducers: {

        showSnackBar: (state)=>{
            state.shown = true
        },
        setSnackBarMessage: (state, action)=>{
            state.message = action.payload
        },
        setSnackbarType: (state, action)=>{
            state.type = action.payload
        }

    }

})

export const snackBarSliceActions = snackBarSlice.actions
export const selectSnackbarShownState = (state)=>state.snackBar.shown
export const selectSnackbarMessage = (state)=>state.snackBar.message
export const selectSnackbarType = (state)=>state.snackBar.type
export default snackBarSlice.reducer

